.card {
  position: relative;
  width: 29%;
  height: 50rem;
  perspective: 150rem;

  @media only screen and (hover: none) {
    width: 29%;
    height: auto;
    box-shadow: 0 1.5rem 2rem rgba($color-black, 0.2);
  }

  @media only screen and (max-width: 56.25em) {
    width: 70%;
    margin: 0 auto;
    // height: auto;
    // box-shadow: 0 1.5rem 2rem rgba($color-black, 0.2);
  }

  &:not(:last-child) {
    @media only screen and (max-width: 56.25em) {
      margin-bottom: 4rem;
    }
  }

  &__side {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50rem;
    border-radius: 3px;
    box-shadow: 0 1.5rem 2rem rgba($color-black, 0.2);
    backface-visibility: hidden;
    overflow: hidden;
    transition: all 0.8s ease;

    @media only screen and (max-width: 56.25em), only screen and (hover: none) {
      position: relative;
      height: auto;
      box-shadow: none;
    }

    &--front {
      background-color: $color-white;
      background-size: cover;
      background-position: bottom bottom;

      @media only screen and (max-width: 56.25em), only screen and (hover: none) {
        padding-bottom: 7rem;
      }
    }

    &--back {
      transform: rotateY(180deg);

      @media only screen and (max-width: 56.25em), only screen and (hover: none) {
        transform: rotateY(0);
        clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
        margin-top: -6rem;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
      &-1 {
        background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark);
      }
      &-2 {
        background-image: linear-gradient(to right bottom, $lightgreen, $darkgreen);
      }
      &-3 {
        background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark);
      }
    }
  }

  &:hover &__side--front {
    transform: rotateY(-180deg);

    @media only screen and (max-width: 56.25em), only screen and (hover: none) {
      transform: rotateY(0);
    }
  }
  &:hover &__side--back {
    transform: rotateY(0);
  }

  //FRONT SIDE STYLING
  &__picture {
    height: 21rem;
    background-size: cover;
    background-blend-mode: screen;
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;

    @media only screen and (max-width: 56.25em), only screen and (hover: none) {
      background-position: center 30%;
    }
    &--1 {
      background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark),
        url(../assets/img/nat-5-small.jpg);

      @media only screen and (min-resolution: 192dpi) and (min-width: 600px),
        only screen and (-webkit-min-device-pixel-ratio: 2) and (min-width: 600px),
        only screen and (min-width: 2000px) {
        background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark),
          url(../assets/img/nat-5.jpg);
      }
    }
    &--2 {
      background-image: linear-gradient(to right bottom, $lightgreen, $darkgreen), url(../assets/img/nat-6-small.jpg);

      @media only screen and (min-resolution: 192dpi) and (min-width: 600px),
        only screen and (-webkit-min-device-pixel-ratio: 2) and (min-width: 600px),
        only screen and (min-width: 2000px) {
        background-image: linear-gradient(to right bottom, $lightgreen, $darkgreen), url(../assets/img/nat-6.jpg);
      }
    }
    &--3 {
      background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark),
        url(../assets/img/nat-7-small.jpg);

      @media only screen and (min-resolution: 192dpi) and (min-width: 600px),
        only screen and (-webkit-min-device-pixel-ratio: 2) and (min-width: 600px),
        only screen and (min-width: 2000px) {
        background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark),
          url(../assets/img/nat-7.jpg);
      }
    }
  }
  &__heading {
    position: absolute;
    top: 11rem;
    right: 2.8rem;
    width: 75%;
    font-size: 2.6rem;
    font-weight: 300;
    text-transform: uppercase;
    text-align: right;
    color: $color-white;

    @media only screen and (max-width: 56.25em) {
      width: 36%;
    }
    @media only screen and (max-width: 660px) {
      width: 40%;
    }
    @media only screen and (max-width: 470px) {
      width: 48%;
    }
    @media only screen and (max-width: 420px) {
      width: 64%;
    }
  }

  &__heading-span {
    padding: 0.7rem 1.5rem;
    box-decoration-break: clone;

    &--1 {
      background-image: linear-gradient(
        to right bottom,
        rgba($color-secondary-light, 0.8),
        rgba($color-secondary-dark, 0.8)
      );
    }

    &--2 {
      background-image: linear-gradient(to right bottom, rgba($lightgreen, 0.8), rgba($darkgreen, 0.8));
    }

    &--3 {
      background-image: linear-gradient(
        to right bottom,
        rgba($color-tertiary-light, 0.8),
        rgba($color-tertiary-dark, 0.8)
      );
    }
  }

  &__details {
    ul {
      list-style: none;
      width: 80%;
      margin: 0 auto;

      li {
        text-align: center;
        font-size: 1.5rem;
        padding: 1rem;

        &:not(:last-child) {
          border-bottom: 1px solid $color-light-grey-2;
        }
      }
    }
  }

  //BACK SIDE STYLING
  &__cta {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    text-align: center;

    @media only screen and (max-width: 56.25em), only screen and (hover: none) {
      position: relative;
      top: 0;
      left: 0;
      transform: translate(0, 0);
      width: 100%;
      padding: 6rem 0;
    }
  }
  &__price-box {
    color: white;
    margin-bottom: 7rem;

    @media only screen and (max-width: 56.25em), only screen and (hover: none) {
      margin-bottom: 4rem;
    }
  }
  &__price-only {
    font-size: 1.4rem;
    text-transform: uppercase;
  }
  &__price-value {
    font-size: 6rem;
    font-weight: 100;

    @media only screen and (max-width: 56.25em), only screen and (hover: none) {
      font-size: 5rem;
    }
  }
}
